    --bs-table-hover-color: #17161A;
    --bs-table-hover-bg: rgba(23, 22, 26, 0.075);
    width: 100%;
    margin-bottom: 3.33333rem;
    color: #17161A;
    vertical-align: top;
    border-color: #dee2e6;
}

.table> :not(caption)>*>* {
    padding: 0.5rem 0.5rem;
    background-color: var(--bs-table-bg);
    border-bottom-width: 1px;
