    display: block;
}

.page-navigation {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 18px;
}

.page-navigation .page-navigation-all {
    position: absolute;
    top: calc(50% - 35px);
    left: calc(50% - 35px);
    display: -webkit-inline-box;
    display: -webkit-inline-flex;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    width: 70px;
    height: 70px;
    color: #F01F4B;
    background-color: #fff;
    border-radius: 50%;
}

.page-navigation .page-navigation-all svg {
    -webkit-transition: -webkit-transform 150ms linear;
    transition: -webkit-transform 150ms linear;
    transition: transform 150ms linear;
    transition: transform 150ms linear, -webkit-transform 150ms linear;
    will-change: transform;
}

.page-navigation .page-navigation-all:hover {
    color: #fff;
    background-color: #F01F4B;
}

.page-navigation .page-navigation-all:hover svg {
    -webkit-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg);
}

.page-navigation .page-navigation-prev,
.page-navigation .page-navigation-next {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 40px;
    margin: 0;
    text-decoration: none;
}

@media (min-width: 828px) {

    .page-navigation .page-navigation-prev,
    .page-navigation .page-navigation-next {
        padding: 50px 70px;
    }
}

.page-navigation .page-navigation-prev svg {
    margin-right: 30px;
}

.page-navigation .page-navigation-next {
    -webkit-box-pack: end;
    -webkit-justify-content: end;
    -ms-flex-pack: end;
    justify-content: end;
    text-align: right;
}

.page-navigation .page-navigation-next svg {
    margin-left: 30px;
}




